@import url(../vendors/normalize.css);
@import url(../../../node_modules/bootstrap/dist/css/bootstrap.css);
#container {
  height: 100vh; }

.no-padding {
  padding: 0px; }

.page {
  padding-top: 80px;
  padding-bottom: 80px; }

.fullpage {
  height: 100vh;
  width: 100%; }

.fullscreen {
  height: 100%; }

.fullscreen.center, .fullpage.center {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center; }

.section {
  width: 100%; }

.section.dark {
  background: #151212;
  color: #ffffff; }

.section.black {
  background: #000000;
  color: #ffffff; }

.section.dim {
  background: #dddddd; }

.section.cyan {
  background: #43c1c7;
  color: #ffffff; }

* {
  font-family: Poppins, Arial, Helvetica, sans-serif; }

.App {
  text-align: center; }

.App-logo {
  animation: App-logo-spin infinite 3s ease-in-out;
  height: 30vmin;
  pointer-events: none; }

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #111111; }

.App-link {
  color: #61dafb; }

@keyframes App-logo-spin {
  0% {
    transform: scale(1); }
  40% {
    transform: scale(0.8); }
  80% {
    transform: scale(1); } }

.image {
  width: 100px;
  height: 100px;
  margin-bottom: 2em; }
  .image img {
    height: 100%;
    width: 100%; }

#home {
  height: 100vh;
  background: url(../images/bg.jpg);
  background-size: cover;
  background-position: center;
  color: #ffffff; }
  #home .name {
    font-size: 3.2em;
    margin-bottom: 0px;
    font-weight: 600; }
  #home .job {
    font-size: 2em;
    font-weight: 300;
    margin-bottom: 1em; }
  #home .desc {
    font-size: 1.4em;
    font-weight: 300; }

.text {
  font-weight: 400; }
  .text.small {
    font-size: 1em; }
  .text.medium {
    font-size: 1.2em; }
  .text.large {
    font-size: 1.4em; }
  .text.bold {
    font-weight: 700; }
  .text.bolder {
    font-weight: 900; }

#navbar {
  padding: 0px; }
  #navbar .navbar-nav > li > a {
    padding-top: 5px !important;
    padding-bottom: 5px !important; }
  #navbar .navbar {
    height: 80px !important; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }
